import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider, AppRoot, Alert } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Result from './panels/Result';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [results, setResults] = useState({});

	const setAlert = (text) => {
		setPopout(
			<Alert
			  actionsLayout="vertical"
			  actions={[
				{
				  title: 'Ок',
				  autoclose: true,
				  mode: 'cancel',
				},
			  ]}
			  onClose={() => setPopout(null)} >
			  {text}
			</Alert>
		  )
	}

	useEffect(() => {
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' fetchedUser={fetchedUser} go={go} setAlert={setAlert} setResults={setResults} />
					<Result id='result' go={go} results={results} />
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
