import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, DatePicker, Group, Checkbox, FormItem, Input, Div, Button, Text, ScreenSpinner } from '@vkontakte/vkui';

const Home = ({ id, go, fetchedUser, setAlert, setResults }) => {
	const [maxDate, setMaxDate] = useState(new Date())
	const [pcModule, setPcModule] = useState(false)
	const [smockModule, setSmockModule] = useState(false)
	const [date, setDate] = useState({day: 26, month: 11, year: 2000})

	const pc = useRef(false)
	const smock = useRef(false)

	const startCalculation = () => {
		let results = {}
		results.date = date
		if (pcModule) {
			if (!+document.getElementById('pc-years').value | !+document.getElementById('pc-hours').value) {
				setAlert('Заполните все поля в разделе о ПК')
				return
			}

			if (+document.getElementById('pc-years').value < 0 | +document.getElementById('pc-hours').value < 0) {
				setAlert('Не верно заполнены поля в разделе о ПК')
				return
			}
			results.pc = {
				y: document.getElementById('pc-years').value,
				h: document.getElementById('pc-hours').value
			}
		}

		if (smockModule) {
			if (!+document.getElementById('smock-years').value | !+document.getElementById('smock-points').value | !+document.getElementById('smock-money').value) {
				setAlert('Заполните все поля в разделе о ПК')
				return
			}

			if (+document.getElementById('smock-years').value < 0 | +document.getElementById('smock-points').value < 0 | +document.getElementById('smock-money').value < 0) {
				setAlert('Не верно заполнены поля в разделе о ПК')
				return
			}
			results.smock = {
				y: document.getElementById('smock-years').value,
				p: document.getElementById('smock-points').value,
				s: document.getElementById('smock-money').value
			}
		}

		setResults(results)
		go({currentTarget: { dataset: { to: 'result'}}})
	}


	if (!fetchedUser) return (
		<Panel id={id}>
			<ScreenSpinner />
		</Panel>
	)

	return (
		<Panel id={id}>
			<PanelHeader>Главная</PanelHeader>
			<Group>
				<Div>
					<Text align='center'>Введите точную дату своего рождения, заполните дополнительную информацию и жмите "Вычислить"!</Text>
				</Div>
			
				<FormItem top="Ваша дата рождения">
        			<DatePicker
        		 		min={{ day: 1, month: 1, year: 1901 }}
        		 		max={{ day: maxDate.getDay(), month: maxDate.getMonth() + 1, year: maxDate.getFullYear()}}

						defaultValue={{ 
							day: fetchedUser.bdate ? +fetchedUser.bdate.split('.')[0] : 1, 
							month: fetchedUser.bdate ? +fetchedUser.bdate.split('.')[1] : 1, 
							year: fetchedUser.bdate ? +fetchedUser.bdate.split('.')[2] ? +fetchedUser.bdate.split('.')[2] : 2000 : 2000 }}
				 		onDateChange={(v) => {
							  setDate(v)
				 		}}
        		 		dayPlaceholder="ДД"
        		 		monthPlaceholder="ММММ"
        		 		yearPlaceholder="ГГГГ"
        			/>
	
      			</FormItem>

				<Checkbox
					getRef={pc}
					onChange={() => setPcModule(pc.current.checked)}>
						Я провожу время за компьютером
				</Checkbox>
				{pcModule ?  
				<div>
					<FormItem top="Сколько лет вы пользуетесь пк">
        				<Input type="number" id='pc-years' min={0} max={100}/>
      				</FormItem>
					<FormItem top="В среднем каждый день (часов)">
					  	<Input type="number" id='pc-hours' min={0} max={24}/>
					</FormItem>
				</div>
				: null}

				<Checkbox
					getRef={smock}
					onChange={() => setSmockModule(smock.current.checked)}>
						Я курю
				</Checkbox>
				{smockModule ?  
				<div>
					<FormItem top="Сколько лет вы курите">
        				<Input type="number" id='smock-years' min={0} max={100} />
      				</FormItem>
					<FormItem top="В среднем каждый день (шт)">
					  <Input type="number" id='smock-points' min={0} max={100} />
					</FormItem>
					<FormItem top="Цена пачки сигорет (руб.)">
					  <Input type="number" id='smock-money' min={0} max={100} />
					</FormItem>
				</div>
				: null}

				<Div align='center'>
					<Button 
						stretched
						align='center'
						size='l'
						mode='outline'
						onClick={() => {
							startCalculation()
						}}>
							Вычислить
					</Button>
				</Div>
			</Group>
		</Panel>
	)
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
